import React, { Component } from 'react';
import TutorialEx from './components/TutorialComponent';
import Footer from './components/FooterComponent';
import wtf from './imgs/wtf2.jpg';
import './App.css';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      counter: 0
    };
  }

  resetCounter() {
    this.setState({counter: 0}, () => console.log("done resetting"));
  }

  handleBackButtonClick(e) {

    if(this.state.counter >= 10) {
      this.resetCounter()
      return;
    };
    
    const tag = document.querySelector('.App')
    this.setState({counter: this.state.counter + 1 }, ()=> console.log("counter++"));
    tag.scrollIntoView();

  }

  render(){
    
    return (
      <div className="App" id="App">

        <button className="BackButton" onClick={() => this.handleBackButtonClick()}>↑</button>

        <div className="Cover">
          <h1>Jaki laptop kupić?</h1>
          <p>Poradnik kupna laptopa</p>
        </div>

        {this.state.counter >= 10 ? <div className="Kotek"><p>Nie bij przyciska :c</p><img src={wtf}></img></div> : null}
        

        <TutorialEx/>

        <Footer/>

      </div>)
    }
}

export default App;
